a {
    text-decoration: none;
    color: #e9e9e9;
}

a:hover,
a:focus {
    color:#FF006B;
}

.content-wrapper {
    display: grid;
    grid-template-columns: 6fr 6fr;
    grid-column-gap: 20px;
    grid-row-gap: 20px;
}

/* ====navbar on landingpage==== */
.NavbarOne {
    display: flex;
    gap: 30px;
    margin-left: 10px;
}

.pdficon {
    height: 25px;
    margin-top: 38px;
    margin-left: -20px;
}

/* =======Linkedin/Github======= */
.NavbarTwo {
    display: flex;
    gap: 20px;
    justify-content: flex-end;
    margin-top: 20px;
    margin-right: 10px;
}

/* =====Text on landingpage===== */
.title {
    align-self: center;
    text-align: right;
    font-size: 24px;
    padding-bottom: 5rem;
}

.graytext {
    color: #A59C9C;
}

.colortext {
    color: #FF006B;
}

